'use client'
import React,{createContext,useContext,useState,ReactNode} from 'react'
type Posting = {
  id:number;
  title: string;
  description: string;
  responsibilities: string[];
  ideal_candidate: {
    age: string;
    gender: string;
    traits: string[];
  };
  when_where: string;
  about: {
    posted_on: Date;
    deadline: Date;
    location: string;
    start_date: string;
    end_date: string;
    categories: string[];
    required_skills: string[];
  };
  company: string;
  image: string;
};
type JobContext={
    selectedJob:Posting|null;
    setSelectedJob:(job:Posting)=>void;
}
const Jobc=createContext<JobContext|undefined>(undefined);
export function Context({children}:{children:ReactNode}) {
    const [selectedJob,setSelectedJob]=useState<Posting|null>(null);
  return (
    <Jobc.Provider value={{selectedJob,setSelectedJob}}>
        {children}
    </Jobc.Provider>
  )
}

export function useJob(){
    const contex=useContext(Jobc);
    if(!contex){
        throw new Error('error in usejob')
    }
    return contex;
}
